<template>
  <div>
    <el-card>
      <div slot="header" class="clearfix">
        <span>工站编号 : {{ CurrentUserInfo.work_id }}</span>
        <span class="ml20">工站名称 : {{ workStationName }}</span>
        <span class="ml20">工站类型 : {{ CurrentUserInfo.work_type }}</span>
      </div>
      <el-form
        class=""
        style="padding: 0 60px 0 20px; width: 650px"
        ref="formRef"
        :model="form"
        :rules="formRules"
        label-width="100px"
      >
        <el-form-item
          prop="response_datas[0].finished_product_code"
          label="产品编号"
          :rules="[{ required: true, message: '产品编号不能为空' }]"
        >
          <el-input
            ref="firstInput"
            v-model="form.response_datas[0].finished_product_code"
            @input="inputChange"
          ></el-input>
        </el-form-item>
        <el-form-item label="检验结果" prop="status">
          <el-select
            v-model="form.response_datas[0].status"
            placeholder="请选择"
            style="width: 100%"
          >
            <el-option label="合格" value="合格"></el-option>
            <el-option label="不合格" value="不合格"></el-option>
          </el-select>
        </el-form-item>
        <el-form-item
          label="备注"
          prop="description"
          v-if="form.status == '不合格'"
        >
          <el-input type="textarea" v-model="form.description"></el-input>
        </el-form-item>
      </el-form>

      <div>
        <el-button @click="resetForm">重 置</el-button>
        <el-button type="primary" @click="dialogSave">确 定</el-button>
      </div>
    </el-card>
  </div>
</template>

<script>
import { baseManagement } from "@/request/api";
import { mapState, mapActions } from "vuex";

export default {
  name: "MissStation",
  data() {
    return {
      timeout: "",
      workStationName: "",
      // 产品过站弹出层
      form: {
        response_datas: [
          {
            finished_product_code: "",
            status: "合格",
          },
        ],
      },
      formBase: {
        response_datas: [
          {
            finished_product_code: "",
            status: "合格",
          },
        ],
      },
    };
  },
  computed: {
    ...mapState([
      "ProductPlanCodeNameList",
      "MatterCodeNameList",
      "BomMatterCodeNameList",
      "WorkCodeNameList",
      "CurrentUserInfo",
    ]),
  },
  methods: {
    ...mapActions([
      "getLogisticProductPlanCodeName",
      "getLogisticMatterNameCode",
      "getLogisticBomMatterCodeName",
      "getLogisticWorkCodeName",
    ]),
    inputChange(value) {},

    // 产品过站 新增
    getLogisticModifyFinishProductStatus(params) {
      baseManagement
        .getLogisticModifyFinishProductStatus(params)
        .then((res) => {
          console.log("产品过站 新增", res);
          if (res.code == 0) {
            this.resetForm();
            this.$message({
              message: res.message,
              type: "success",
            });
          } else {
            this.$message({
              message: res.message,
              type: "error",
            });
          }
        });
    },

    // 重置按钮
    resetForm() {
      this.form = JSON.parse(JSON.stringify(this.formBase));
      this.$nextTick(() => {
        this.$refs.formRef.clearValidate();
      });
    },

    // 确认按钮
    dialogSave() {
      console.log("this.form", this.form);

      this.$refs.formRef.validate((valid) => {
        if (!valid) {
          this.$message({
            message: "请修改正确的数据格式！",
            type: "warning",
          });
          return;
        } else {
          //验证通过调用保存接口
          console.log("产品过站", this.form);
          let params = JSON.parse(JSON.stringify(this.form));
          params.response_datas = JSON.stringify(this.form.response_datas);
          this.getLogisticModifyFinishProductStatus(params);
        }
      });
    },
  },
  mounted() {
    // 工站内码和工站名称接口： 查询
    this.getLogisticWorkCodeName();
    this.WorkCodeNameList.forEach((CodeNameListItem) => {
      if (this.CurrentUserInfo.work_id == CodeNameListItem.work_id) {
        this.workStationName = CodeNameListItem.work_name;
      }
    });
    this.$nextTick(() => {
      this.$refs.firstInput.focus();
    });
  },
};
</script>

